@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700,900");
header {
  position: relative;
}
header .header__contenedor {
  background-color: rgba(0, 0, 0, 0.7);
  padding: 2rem;
}
header .ocultar {
  display: none !important;
}
header .header__descripcion {
  position: absolute;
  z-index: 1;
  top: 35%;
  left: 5%;
  background-color: rgba(0, 0, 0, 0.7);
  border-radius: 2rem;
  width: 30rem;
  padding: 3rem 0 3rem 2rem;
}
@media (min-width: 768px) {
  header .header__descripcion {
    top: 20rem;
    left: 15rem;
    width: 45rem;
  }
}
@media (min-width: 1024px) {
  header .header__descripcion {
    top: 25rem;
    left: 25rem;
  }
}
header .header__barra {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
header .header__enlace--principal {
  margin-right: 4rem;
  font-size: 3.7rem;
  color: #FFFFFF;
  font-weight: 900;
}
header .header__enlace--principal:hover {
  opacity: 0.5;
  cursor: pointer;
}
header .header__video {
  width: 100%;
  display: block;
}

.header__descripcion p {
  font-size: 1.5rem;
  color: #FFFFFF;
  font-weight: 900;
  margin-left: 2rem;
}
@media (min-width: 768px) {
  .header__descripcion p {
    font-size: 2.7rem;
  }
}
.header__descripcion p:before {
  content: "";
  background-color: #FFFFFF;
  width: 0.5rem;
  height: 3rem;
  display: block;
  background-repeat: no-repeat;
  rotate: y 90deg;
  position: absolute;
  left: 2rem;
  top: 9rem;
}
@media (min-width: 768px) {
  .header__descripcion p:before {
    height: 6rem;
  }
}
.header__descripcion h1 {
  font-size: 1.5rem;
  color: #e1e1e1;
}
.header__descripcion .flex {
  margin-top: 2rem;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 768px) {
  .header__descripcion .flex {
    margin-top: 0;
    flex-direction: row;
  }
}
.header__descripcion a {
  margin-right: 2rem;
}
@media (min-width: 768px) {
  .header__descripcion a {
    margin-right: 1rem;
  }
  .header__descripcion a:last-of-type {
    margin-right: 0;
  }
}

.header__enlace {
  color: #FFFFFF;
  margin-right: 2rem;
  font-size: 2rem;
  font-weight: 900;
}
.header__enlace:hover {
  opacity: 0.5;
  cursor: pointer;
}
.header__enlace:last-of-type {
  margin-right: 0;
}

.header__menu {
  display: flex;
  flex-direction: column;
}
.header__menu .mobile-menu {
  display: block;
  width: 5rem;
}
@media (min-width: 510px) {
  .header__menu .mobile-menu {
    top: 5rem;
  }
}
@media (min-width: 768px) {
  .header__menu .mobile-menu {
    display: none;
  }
}
.header__menu .mobile-menu:hover {
  cursor: pointer;
  opacity: 0.5;
}
.header__menu .header__navegacion {
  display: flex;
  flex-direction: column;
  visibility: hidden;
  opacity: 0;
  background-color: rgba(0, 0, 0, 0.7);
  padding: 2rem 1rem;
  position: absolute;
  top: 8rem;
  z-index: 2;
}
@media (min-width: 768px) {
  .header__menu .header__navegacion {
    position: inherit;
    background-color: transparent;
    padding: 0;
    flex-direction: row;
    visibility: visible;
    opacity: 1;
  }
}
.header__menu .mostrar {
  visibility: visible;
  opacity: 1;
}